var localStorage = require('web-storage')().localStorage

export const apiDomain = 'http://localhost:8000/'
//export const apiDomain = 'http://192.168.8.104:8000/'
export const loginUrl = apiDomain + 'oauth/token'
export const userUrl = apiDomain + 'api/user'
//site url
export const siteAddUrl = apiDomain + 'api/site'
export const siteIndexUrl = apiDomain + 'api/site'
export const siteShowUrl = apiDomain + 'api/site'
//type analyse url
export const typeAnalyseAddUrl = apiDomain + 'api/type_analyse'
export const typeAnalyseIndexUrl = apiDomain + 'api/type_analyse'
export const typeAnalyseShowUrl = apiDomain + 'api/type_analyse'
export const typeAnalyseDeleteUrl = apiDomain + 'api/type_analyse'
//parametre type analyse
export const paramTypeAnalyseShowUrl = apiDomain + 'api/type_analyse/parametre'
//puit url
export const puitAddUrl = apiDomain + 'api/puit'
export const puitIndexUrl = apiDomain + 'api/puit'
export const puitShowUrl = apiDomain + 'api/puit'

export const echantillonUrl = apiDomain + 'api/echantillon'

//get header to authentification
export const getHeader = function () {
  const tokenData = localStorage.get('authUser')
  const headers = {
    'Accept': 'application/json',
    'Authorization': 'Bearer '+ tokenData.access_token
  }
  return headers
}



